.rmdb-moviethumb img {
  width: 100%;
  height: auto;
  transition: all 0.3s;
  object-fit: cover;
  border-radius: 20px;
  border: 4px solid #ce9071;
    border-radius: 24px;
}
.rmdb-moviethumb button{
  margin: 10px 10px;
}

.clickable {
  width: 200px !important;
  cursor: pointer;
}

.clickable:hover {
  opacity: 0.8;
}


