@import url(https://fonts.googleapis.com/css2?family=Jost:wght@100;300;400;700&display=swap);
.rmdb-header {
  width: 100%;
  height: auto;
  background: #1c1c1c;
  padding: 0 20px;
  box-sizing: border-box;
}

.rmdb-header-content {
  max-width: 1280px;
  min-height: 120px;
  height: auto;
  padding-top: 20px;
  margin: 0 auto;
  box-sizing: border-box;
  overflow: hidden;
}

.rmdb-logo {
  width: 100px;
  margin-top: 20px;
  object-fit: contain;
  float: left;
}

.rmdb-tmdb-logo {
  width: 122px;
  margin: 25px 10px;
  float: right;
}

@media screen and (max-width: 500px) {
  .rmdb-header-content {
    max-width: 1280px;
    min-height: 0px;
  }

  .rmdb-tmdb-logo {
    display: inline-block;
    width:80px;
    margin: 0 10px;
  }

  .rmdb-logo {
    width: 150px;
    margin-top: 5px;
  }
}
#root{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.rmdb-searchbar {
  width: 100%;
  position: relative;
  box-sizing: border-box;
  color: #fff;
  margin-bottom: 30px;
}

.rmdb-actionbar {
  width: 100%;
  position: relative;
  box-sizing: border-box;
  margin-bottom: 15px;
}

.rmdb-searchbar-content {
  max-width: 1280px;
  width: 90%;
  height: 55px;
  background:#353535;
  margin: 0 auto;
  border-radius: 40px;
  position: relative;
  color: #fff;
}
.rmdb-action-content {
  max-width: 1280px;
  width: 90%;
  height: 55px;
  background:#353535;
  margin: 0 auto;
  border-radius: 40px;
  position: relative;
}

.rmdb-fa-search {
  position: absolute;
  left: 20px;
  top: 12px;
  color: #fff;
}

.rmdb-searchbar-input {
  font-family: 'Abel', sans-serif;
  font-size: 38px;
  position: relative;
  margin: 8px 0 0 60px;
  border: 0;
  background: transparent;
  height: 40px;
  width: 90%;
  color: #fff;
}

.rmdb-searchbar-input:focus {
  outline: none;
}

@media screen and (max-width: 720px) {
  .rmdb-searchbar-input {
    margin: 8px 0 0 60px;
    font-size: 28px;
    color: #fff;
  }
}

.rmdb-grid h1 {
  font-family: 'Abel', sans-serif;
  font-size:42px;
}

.rmdb-grid-content {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 40px;
  position: relative;
}

.rmdb-grid-element {
  -webkit-animation: animateGrid 0.5s;
          animation: animateGrid 0.5s;
  /*height: 400px;*/
  /*display: flex;*/
  /*flex-direction: column;*/
  /*justify-content: space-between;*/
}

@-webkit-keyframes animateGrid {
  from {
      opacity:0;
  }
  to {
      opacity:1;
  }
}

@keyframes animateGrid {
  from {
      opacity:0;
  }
  to {
      opacity:1;
  }
}

@media screen and (max-width: 768px) {
  .rmdb-grid-content {
    grid-template-columns: repeat(2, 1fr);
  }

  .rmdb-grid h1 {
    font-size:22px;
  }
}

@media screen and (max-width: 375px) {
  .rmdb-grid-content {
    grid-template-columns: repeat(1, 1fr);
  }
}

.rmdb-moviethumb img {
  width: 100%;
  height: auto;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  object-fit: cover;
  border-radius: 20px;
  border: 4px solid #ce9071;
    border-radius: 24px;
}
.rmdb-moviethumb button{
  margin: 10px 10px;
}

.clickable {
  width: 200px !important;
  cursor: pointer;
}

.clickable:hover {
  opacity: 0.8;
}



.rmdb-loadmorebtn {
  background: #16d47b;
  width: 150px;
  /*min-height: 50px;*/
  text-align: center;
  margin:30px  auto 0;
  color: #fff;
  padding: 7px 15px;
  cursor: pointer;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.rmdb-loadmorebtn h6 {
  font-family: 'Abel', sans-serif;
  font-size:25px;
  /*padding: 20px;*/
}

.rmdb-loadmorebtn:hover {
  opacity: 0.8;
}

.loader {
  border: 5px solid #f3f3f3; /* Light grey */
  border-top: 5px solid #16d47b; /* Blue */
  border-radius: 50%;
  width: 50px;
  height: 50px;
  -webkit-animation: spin 0.8s linear infinite;
          animation: spin 0.8s linear infinite;
  margin: 20px auto;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.rmdb-home {
  margin-bottom: 100px;
}

.rmdb-home-grid {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 20px;
}

.rmdb-navigation {
  display: flex;
  align-items: center;
  width: 100%;
  height: 70px;
  background: #353535;
  color: #fff;
}

.rmdb-navigation-content {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 20px;
  width: 100%;
}

.rmdb-navigation-content p {
  font-family: 'Abel', sans-serif;
  font-size: 22px;
  float: left;
  color: #fff;
  padding-right: 10px;
  text-decoration: none;
}

@media screen and (max-width: 768px) {
  .rmdb-navigation-content p {
    font-size: 16px;
  }
}
.rmdb-movieinfo {
  background-size: cover !important;
  background-position: center !important;
  width: 100%;
  padding: 40px 20px;
  box-sizing: border-box;
  -webkit-animation: animateMovieinfo 1s;
          animation: animateMovieinfo 1s;
}

@-webkit-keyframes animateMovieinfo {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes animateMovieinfo {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.rmdb-movieinfo-content {
  max-width: 1280px;
  min-height: 450px;
  margin: 0 auto;
  background: rgb(0, 0, 0, 0.7);
  border-radius: 20px;
  position: relative;
}

.rmdb-movieinfo-thumb {
  width: 300px;
  float: left;
}

.rmdb-movieinfo-text {
  font-family: Arial, Helvetica, sans-serif;
  padding: 40px;
  color: #fff;
  overflow: hidden;
}

.rmdb-movieinfo-text h1 {
  font-family: 'Abel', sans-serif;
  font-size: 48px;
  margin: 0;
}

.rmdb-movieinfo-text h3 {
  font-size: 16px;
  line-height: 0;
  margin-top: 30px;
}

.rmdb-movieinfo-text p {
  font-family: 'Abel', sans-serif;
  font-size: 18px;
  line-height: 26px;
}

.rmdb-rating {
  width: 190px;
  height: 20px;
  margin-top: 20px;
  position: relative;
}

.rmdb-score {
  position: absolute;
  margin: 0;
  right: 0px;
  top: -3px;
}

.rmdb-director {
  margin: 0;
}

.fa-film {
  position: absolute;
  bottom: 40px;
  right: 40px;
  color: #fff;
}

meter::-webkit-meter-bar {
  background: #fff;
  width: 150px;
}
meter::-webkit-meter-optimum-value {
  background: -webkit-gradient(linear, left top, left bottom, from(#16d47b)) !important;
  background: linear-gradient(to bottom, #16d47b) !important;
}
meter::-webkit-meter-suboptimum-value {
  background: -webkit-gradient(linear, left top, left bottom, from(#fbb450)) !important;
  background: linear-gradient(to bottom, #fbb450) !important;
}
meter::-webkit-meter-even-less-good-value {
  background: -webkit-gradient(linear, left top, left bottom, from(#ee5f5b)) !important;
  background: linear-gradient(to bottom, #ee5f5b) !important;
}

@media screen and (max-width: 1000px) {
  .rmdb-movieinfo-text h1 {
    font-size: 32px !important;
  }
}

@media screen and (max-width: 768px) {
  .rmdb-movieinfo {
    min-height: 600px;
    height: auto;
  }

  .rmdb-movieinfo-thumb {
    width: 100% !important;
  }

  .fa-film {
    display: none;
  }

  .rmdb-movieinfo-text h1 {
    font-size: 32px;
  }  
}

.rmdb-movie {
  margin-bottom: 100px;
}

.rmdb-movie-grid {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 20px;
}
