.modal-panel{
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    position: absolute;
    background: rgba(255, 255, 255, 0.21);
}

.input-modal{
    z-index: 999;
    position: absolute;
    background: white;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    padding: 15px;
    border-radius: 12px;
    width: 50%;
    opacity: 1;
}

.input-modal input{
    padding: 15px;
    margin: 0 0;
    width: 100%;
    background: #686767;
    color: white;
}
.input-modal > label{
    font-weight: 500;
    font-size: 15px;
    margin-bottom: 10px;
}

